<template>
  <div class="poc">
    <div>{{ PEER_ID }}:{{ store.name }}</div>
    <hr />
    <div v-for="user in store.users" :key="user.id">{{ user.displayName }}</div>
    <div class="flex">
      <video muted id="vod" width="460" height="306" />
      <video muted id="me-vod" width="460" height="306" />
    </div>
  </div>
</template>
<script>
import protooClient from 'protoo-client'
import * as mediasoupClient from 'mediasoup-client'
import { onMounted, reactive } from 'vue'
import Lazy from 'lazy.js'
const PEER_ID = ~~(Math.random() * 1000)
const SERVICE = `wss://dev.webrtc.anchorgo.io:4443/?roomId=${new URLSearchParams(
  new URL(window.location.href).search,
).get('room') || 'anchortech'}&peerId=${PEER_ID}`
// const SERVICE = `wss://dev.webrtc.anchorgo.io:4443/?roomId=anchortech&peerId=${PEER_ID}`
const PC_PROPRIETARY_CONSTRAINTS = {
  optional: [{ googDscp: true }],
}
export default {
  name: 'Poc',
  setup() {
    const store = reactive({
      recvTransport: null,
      name:
        new URLSearchParams(new URL(window.location.href).search).get('name') ||
        'No Name',
      users: null,
      track: null,
    })
    const protooTransport = new protooClient.WebSocketTransport(SERVICE)
    const _protoo = new protooClient.Peer(protooTransport)
    _protoo.on('open', () => console.log('#open'))
    _protoo.on('failed', () => console.log('#failed'))
    _protoo.on('disconnected', () => console.log('#disconnected'))
    _protoo.on('close', () => console.log('#close'))
    // _protoo.on('request', async (request, accept, reject) => {
    _protoo.on('request', async (request, accept) => {
      // console.log('#request', request.method, accept, reject)
      console.log('#request', request.method)
      if (request.method === 'newConsumer') {
        const {
          peerId,
          producerId,
          id,
          kind,
          rtpParameters,
          // type,
          appData,
          // producerPaused,
        } = request.data
        const consumer = await store.recvTransport.consume({
          id,
          producerId,
          kind,
          rtpParameters,
          appData: { ...appData, peerId }, // Trick.
        })
        consumer.on('transportclose', () => {
          console.log('#consumer-transportclose')
        })
        const {
          spatialLayers,
          // temporalLayers,
        } = mediasoupClient.parseScalabilityMode(
          consumer.rtpParameters.encodings[0].scalabilityMode,
        )
        console.log('#spatialLayers', spatialLayers, consumer)

        // if (spatialLayers == 3) { //這裡只有視訊...XD
        console.log('#視訊')
        store.track = consumer.track
        const stream = new MediaStream()
        stream.addTrack(consumer.track)
        document.getElementById('vod').srcObject = stream
        document.getElementById('vod').play()
        // }
        accept()
      } else if (request.method === 'newDataConsumer') {
        const {
          peerId, // NOTE: Null if bot.
          dataProducerId,
          id,
          sctpStreamParameters,
          label,
          protocol,
          appData,
        } = request.data
        const dataConsumer = await store.recvTransport.consumeData({
          id,
          dataProducerId,
          sctpStreamParameters,
          label,
          protocol,
          appData: { ...appData, peerId }, // Trick.
        })
        dataConsumer.on('transportclose', () =>
          console.log('#dataConsumer_transportclose'),
        )
        dataConsumer.on('open', data => console.log('#dataConsumer_open', data))
        dataConsumer.on('close', () => console.log('#dataConsumer_close'))
        dataConsumer.on('error', () => console.log('#dataConsumer_error'))
        dataConsumer.on('message', () => console.log('#dataConsumer_message'))
      }
    })
    _protoo.on('notification', notification => {
      // console.log('#notification', notification) //TODO: 太多訊號了
      switch (notification.method) {
        case 'newPeer':
          console.log('#newPeer', notification)
          store.users.push(notification.data)
          break
        case 'peerClosed':
          console.log('#peerClosed', notification)
          store.users =
            Lazy(store.users)
              .filter(user => {
                return user.id != notification.data.peerId
              })
              .toArray() || []
          break
        case 'newConsumer':
          console.log('#newConsumer-xxxxxxx')
          break
        case 'downlinkBwe':
        case 'activeSpeaker':
          break
        default:
          console.log('#notification 未定義', notification)
      }
    })

    onMounted(async () => {
      await delay(1000)
      try {
        const _mediasoupDevice = new mediasoupClient.Device({
          handlerName: '',
        })
        const routerRtpCapabilities = await _protoo.request(
          'getRouterRtpCapabilities',
        )
        await _mediasoupDevice.load({ routerRtpCapabilities })
        const transportInfo = await _protoo.request('createWebRtcTransport', {
          forceTcp: false,
          producing: true,
          consuming: false,
          sctpCapabilities: _mediasoupDevice.sctpCapabilities,
        })
        const {
          id,
          iceParameters,
          iceCandidates,
          dtlsParameters,
          sctpParameters,
        } = transportInfo

        const _sendTransport = _mediasoupDevice.createSendTransport({
          id,
          iceParameters,
          iceCandidates,
          dtlsParameters,
          sctpParameters,
          iceServers: [],
          proprietaryConstraints: PC_PROPRIETARY_CONSTRAINTS,
        })

        _sendTransport.on(
          'connect',
          (
            { dtlsParameters },
            callback,
            errback, // eslint-disable-line no-shadow
          ) => {
            console.log('#connect')
            _protoo
              .request('connectWebRtcTransport', {
                transportId: _sendTransport.id,
                dtlsParameters,
              })
              .then(callback)
              .catch(errback)
          },
        )
        _sendTransport.on(
          'produce',
          async ({ kind, rtpParameters, appData }, callbackfunc, errback) => {
            console.log('#produce')
            try {
              const { id } = await _protoo.request('produce', {
                transportId: _sendTransport.id,
                kind,
                rtpParameters,
                appData,
              })

              callbackfunc({ id })
            } catch (error) {
              errback(error)
            }
          },
        )
        _sendTransport.on('producedata', () => console.log('#producedata'))
        _sendTransport.on('connectionstatechange', connectionState =>
          console.log('#connectionstatechange', connectionState),
        )

        const enableMic = async () => {
          const stream = await navigator.mediaDevices.getUserMedia({
            video: true,
          })
          // stream.addTrack(track)
          document.getElementById('me-vod').srcObject = stream
          document.getElementById('me-vod').play()

          const track = stream.getVideoTracks()[0]
          await _sendTransport.produce({
            track,
          })
        }
        enableMic()

        {
          const transportInfo = await _protoo.request('createWebRtcTransport', {
            forceTcp: false,
            producing: false,
            consuming: true,
            sctpCapabilities: _mediasoupDevice.sctpCapabilities,
          })
          const {
            id,
            iceParameters,
            iceCandidates,
            dtlsParameters,
            sctpParameters,
          } = transportInfo
          const _recvTransport = _mediasoupDevice.createRecvTransport({
            id,
            iceParameters,
            iceCandidates,
            dtlsParameters,
            sctpParameters,
            iceServers: [],
          })
          store.recvTransport = _recvTransport
          _recvTransport.on(
            'connect',
            (
              { dtlsParameters },
              callback,
              errback, // eslint-disable-line no-shadow
            ) => {
              console.log('#connect 2')
              _protoo
                .request('connectWebRtcTransport', {
                  transportId: _recvTransport.id,
                  dtlsParameters,
                })
                .then(callback)
                .catch(errback)
            },
          )
        }

        const { peers } = await _protoo.request('join', {
          displayName: store.name,
          device: {},
          rtpCapabilities: _mediasoupDevice.rtpCapabilities,
          sctpCapabilities: _mediasoupDevice.sctpCapabilities,
        })
        console.log('#peers', peers)
        store.users = peers
      } catch (e) {
        console.error('#error', e)
      }
    })

    return {
      PEER_ID,
      store,
    }
  },
}

function delay(_time = 0) {
  return new Promise(resolve => {
    setTimeout(resolve, _time)
  })
}
</script>
<style lang="postcss" scoped>
#me-vod {
  /* position: absolute;
  left: 0;
  top: 0; */
}
.flex {
  display: flex;
}
</style>
